import React from 'react';
import Button from './Button.jsx';
import Display from  './Display.jsx';
import './main.css';

class Calculator extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            prevDisplay: "", 
            currDisplay: 0,
            eval: false
        }
        this.handleClick=this.handleClick.bind(this);
        this.updateDisplay=this.updateDisplay.bind(this);
        this.clearDisplay=this.clearDisplay.bind(this);
    }
    handleClick(e){
        this.updateDisplay(e);
    }
    updateDisplay(e){
        if(/^0/.test(this.state.currDisplay)){
            if(!/0|=|\*|-|\+|\/|\./.test(e.target.innerText)){
                if(!/^0\./.test(this.state.currDisplay)){
                    this.setState({currDisplay: e.target.innerText});
                }else{
                    this.setState({currDisplay: this.state.currDisplay + e.target.innerText});
                }  
            }else if(/=/.test(e.target.innerText)&&/\d/.test(this.state.prevDisplay)){
                let toEval = /\+$|\*$|\-$|\+$|\/$|\=$/gm.test(this.state.prevDisplay)&&this.state.currDisplay === 0 ? this.state.prevDisplay.slice(0, -1) : this.state.prevDisplay + this.state.currDisplay;
                this.setState({prevDisplay: this.state.prevDisplay + this.state.currDisplay, currDisplay: eval(toEval), eval: true});
            }else if(/\*|-|\+|\//.test(e.target.innerText)){
                if(!this.state.prevDisplay===""&&!this.state.currDisplay===0){
                    this.setState({prevDisplay: this.state.prevDisplay + this.state.currDisplay + e.target.innerText, currDisplay: 0});
                }else if(/\*$|-$|\+$|\/$/.test(this.state.prevDisplay)){
                    if(/\*-$|\+-$|\/-$/.test(this.state.prevDisplay)){
                        this.setState({prevDisplay: this.state.prevDisplay.slice(0, -2) + e.target.innerText})
                    }else if(!/-/.test(e.target.innerText)){
                        this.setState({prevDisplay: this.state.prevDisplay.slice(0, -1) + e.target.innerText})
                    }else if(!/-$/.test(this.state.prevDisplay)){
                        this.setState({prevDisplay: this.state.prevDisplay + e.target.innerText})
                    }                    
                }else if(/^0\./.test(this.state.currDisplay)){
                    this.setState({prevDisplay: this.state.currDisplay + e.target.innerText});
                }
            }else if(/\./.test(e.target.innerText)){
                this.setState({
                    currDisplay: 0 + e.target.innerText
                })
            }
        }else{
            if(/\d/.test(e.target.innerText)){
                this.setState({currDisplay: this.state.currDisplay + e.target.innerText})
            }else if(/\*|-|\+|\//.test(e.target.innerText)){
                if(!this.state.eval){
                    this.setState({prevDisplay: this.state.prevDisplay + this.state.currDisplay + e.target.innerText, currDisplay: 0});
                }else{
                    this.setState({prevDisplay: this.state.currDisplay + e.target.innerText, currDisplay: 0 , eval: false})
                }                
            }else if(/=/.test(e.target.innerText)){
                let toEval = /\*|-|\+|\/|\=/.test(this.state.prevDisplay)&&this.state.currDisplay === 0 ? this.state.prevDisplay.slice(0, -1) : this.state.prevDisplay + this.state.currDisplay;
                this.setState({prevDisplay: this.state.prevDisplay + this.state.currDisplay, currDisplay: eval(toEval), eval: true});
            }else if(/\./.test(e.target.innerText)){
                if(!/\./g.test(this.state.currDisplay)){
                    this.setState({currDisplay: this.state.currDisplay + e.target.innerText})
                }
            }
        }
    }
    clearDisplay(){
        this.setState({
            currDisplay: 0,
            prevDisplay: ""
        })
    }
    render(){
        return <div className="calculator">
            <Display currDisplay={this.state.currDisplay} prevDisplay={this.state.prevDisplay} /> 
            <div class="numBoard">       
                <Button id="seven" className="button button__numBoard" onClick={this.handleClick} text={7} />
                <Button id="eight" className="button button__numBoard" onClick={this.handleClick} text={8} />
                <Button id="nine" className="button button__numBoard" onClick={this.handleClick} text={9} />
                <Button id="four" className="button button__numBoard" onClick={this.handleClick} text={4} />
                <Button id="five" className="button button__numBoard" onClick={this.handleClick} text={5} />
                <Button id="six" className="button button__numBoard" onClick={this.handleClick} text={6} />
                <Button id="one" className="button button__numBoard" onClick={this.handleClick} text={1} />
                <Button id="two" className="button button__numBoard" onClick={this.handleClick} text={2} />
                <Button id="three" className="button button__numBoard" onClick={this.handleClick} text={3} />
                <Button id="zero" className="button button__zero button__numBoard" onClick={this.handleClick} text={0} />
                <Button id="decimal" className="button button__numBoard" onClick={this.handleClick} text={'.'} />
            </div>
            <div className="opBoard">
                <Button id="clear" className="button button__AC" onClick={this.clearDisplay} text={'AC'} />
                <Button id="multiply" className="button button__opBoard" onClick={this.handleClick} text={'*'} />
                <Button id="divide" className="button button__opBoard" onClick={this.handleClick} text={'/'} />
                <Button id="add" className="button button__opBoard" onClick={this.handleClick} text={'+'} />
                <Button id="equals" className="button button__equals button__opBoard" onClick={this.handleClick} text={'='} />
                <Button id="subtract" className="button button__opBoard" onClick={this.handleClick} text={'-'} />
            </div>
        </div>
    }
}

export default Calculator;